import { SessionUtil, DateUtil } from 'utils';

export const Env = process.env.REACT_APP_ENV || 'dev';

// Local  
const isLocal = () => {
  const { hostname } = window.location;
  switch (hostname) {
    case 'localhost':
    case '127.0.0.1':
      return true;
  }
  return false;
};

// TestBed 
const isTB = () => {
  const { hostname } = window.location;
  switch (hostname) {
    case 'egg.pigplanxe.co.kr':
      return true;
  }
  return false;
};

