import { observable, action, configure, computed } from 'mobx';
import { SessionUtil } from 'utils';
configure({ enforceActions: 'observed' });

/*
   Store .
       .
*/
class MobileStore {
  constructor(root) {
    this.root = root;
  }

  @observable headerYn = false; //  
  @observable popupStep = false; //   :     

  @observable logined = false; //  
  @observable autoLogin = false; //  
  @observable userId = ''; // 
  @observable userPw = ''; // 
  @observable pplYn = false; //  PPL 
  @observable titleIcon = ''; //     
  @observable title = ''; //     
  @observable farmCode = ''; //   FarmCode

  @observable rcvSelectModalShow = false; // Receive    on/off

  //  on/off |
  // cate [S: , A:, C:] --->Modal 
  //type [S: (save), D: (delete), B:(back), LO:]--->   
  @observable modalFlag = {
    onoff: false,
    cate: '',
    type: '',
    msg: '',
    params: '',
  };

  @observable searchResultList = []; // 

  //   S: strain  / C: Customer / F:Feed, / V: Vaccine,
  // M: Medicine / TM: Total Material
  @observable searchType = '';
  @observable searchKey = ''; // 
  @observable searchRowCnt = 0; // 
  @observable searchTotal = 0; //  
  @observable searchPageNo = 1; //  

  @observable menuName = ''; // Footer navigation  
  @observable iuFlag = 'I'; // inser& update Flag

  @observable dynamicInputNo = 1; //      

  @observable appYn = 'N'; // App  

  @action
  isMobile = () => {
    return SessionUtil.isMobile();
  };

  @action
  setLogined = () => {
    this.logined = !this.logined;
  };

  @action
  setAutoLogin = () => {
    this.autoLogin = !this.autoLogin;
  };

  @action
  setUserId = (value) => {
    this.userId = value;
  };

  @action
  setUserPw = (value) => {
    this.userPw = value;
  };

  @action
  setPplYn = () => {
    this.pplYn = !this.pplYn;
  };

  @action
  setTitleIcon = (value) => {
    this.titleIcon = value;
    sessionStorage.setItem('titleIcon', value);
  };

  @action
  setTitle = (value) => {
    this.title = value;
    sessionStorage.setItem('title', value);
  };

  @action
  setFarmCode = (value) => {
    this.farmCode = value;
  };

  @action
  setRcvSelectModalOpen = () => {
    this.rcvSelectModalShow = true;
  };
  @action
  setRcvSelectModalClose = () => {
    this.rcvSelectModalShow = false;
  };

  @action
  setPopupStepNextTrue = () => {
    this.popupStep = true;
    sessionStorage.setItem('popupStep', true);
  };

  @action
  setPopupStepNextFalse = () => {
    this.popupStep = false;
    sessionStorage.setItem('popupStep', false);
  };

  @action
  setModalOpen = (cate, type, msg, params) => {
    this.modalFlag = {
      onoff: true,
      cate: cate,
      type: type,
      msg: msg,
      params: params,
    };
  };

  @action
  setModalClose = () => {
    this.modalFlag = { onoff: false, cate: '', type: '', msg: '', params: '' };
  };

  @action
  setSearchResultList = (value) => {
    this.searchResultList = value;
  };

  @action
  setSearchType = (value) => {
    this.searchType = value;
    sessionStorage.setItem('searchType', value);
  };

  @action
  setMenuName = (value) => {
    this.menuName = value;
    sessionStorage.setItem('menuName', value);
  };

  @action
  setIuFlagUpdate = () => {
    this.iuFlag = 'U';
    sessionStorage.setItem('iuFlag', 'U');
  };

  @action
  setIuFlagInsert = () => {
    this.iuFlag = 'I';
    sessionStorage.setItem('iuFlag', 'I');
  };

  @action
  setSearchKey = (value) => {
    this.searchKey = value;
  };

  @action
  setSearchTotal = (value) => {
    this.searchTotal = value;
  };

  @action
  setSearchRowCnt = (value) => {
    this.searchRowCnt = value;
  };

  @action
  setSearchPageNo = (value) => {
    this.searchPageNo = value;
  };

  @action
  setSdynamicInputNo = (value) => {
    this.dynamicInputNo = value;
  };

  @action
  setAppYn_Y = () => {
    this.appYn = 'Y';
  };

  @action
  setAppYn_N = () => {
    this.appYn = 'N';
  };

  @action
  initStore = () => {
    this.popupStep = false; //  
    this.confirmModalShow = { onoff: false, cate: '', type: '', msg: '' }; //   on/off |  type [S: (save), D: (delete), B:(back)]
    this.searchResultList = []; // 
    this.searchType = ''; //   S: strain  / C: Customer /
    this.searchKey = ''; // 
    this.searchRowCnt = 0;
    this.searchTotal = 0;
    this.searchPageNo = 1;
    this.menuName = ''; // Footer navigation  
    this.iuFlag = 'I'; // inser& update Flag
    this.dynamicInputNo = 1;
  };
}

export default new MobileStore();
